<template>
	<div class="MainPage" v-bind:class = "$route.name == 'Dashboard'?'admin-layout':'hello'">
		
		<Header v-show = "$route.name != 'SignupForm' && $route.name != 'SigninForm' && $route.name != 'Dashboard'"/>
		<!-- END: Mobile Menu -->
        <div class="flex mt-[4.7rem] md:mt-0 overflow-hidden" v-show = "$route.name == 'Dashboard'" >
		<Sidebar/>
		
		<div class="content">
		<AdminHeader />
		<router-view />
		</div>
		</div>
		<router-view v-show = "$route.name != 'Dashboard'"/>
		<Footer v-show = "$route.name != 'SignupForm' && $route.name != 'SigninForm' && $route.name != 'Dashboard'" />
	</div>
</template>

<script>

import Header from '@/components/Header.vue';

import Footer from '@/components/Footer.vue';


export default {
	name:"main_page",

	components: {
	    
		Header,
		
		Footer
	},
	data () {
    return {
      msg: window.location.hash
    }
  }
}
</script>


